:root {
  --brand: #6c5ce7;
  --text: CanvasText;
  --bg: Canvas;
}

* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  color: var(--text);
  background: var(--bg);
  margin: 0;
  font: 16px / 1.6 system-ui, -apple-system, Segoe UI, Roboto, sans-serif;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.container {
  max-width: 1100px;
  margin-inline: auto;
  padding-inline: 16px;
}

.section {
  padding: 80px 0;
  scroll-margin-top: 80px;
}

.grid {
  gap: 16px;
  display: grid;
}

.grid-2 {
  grid-template-columns: 1fr;
  gap: 24px;
}

@media (min-width: 820px) {
  .grid-2 {
    grid-template-columns: 1.2fr .8fr;
  }
}

.grid--3 {
  grid-template-columns: 1fr;
}

@media (min-width: 700px) {
  .grid--3 {
    grid-template-columns: repeat(3, 1fr);
  }
}

.brand-color {
  color: var(--brand);
}

.muted {
  opacity: .75;
}

.btn {
  border: 1px solid color-mix(in oklab, var(--text) 20%, transparent);
  background: none;
  border-radius: 12px;
  padding: 10px 14px;
  display: inline-block;
}

.btn--primary {
  background: var(--brand);
  color: #fff;
  border-color: var(--brand);
  cursor: pointer;
}

:focus-visible {
  outline: 3px solid var(--brand);
  outline-offset: 2px;
}

.skip-link {
  position: absolute;
  top: auto;
  left: -999px;
}

.skip-link:focus {
  background: var(--bg);
  z-index: 999;
  border-radius: 8px;
  padding: .5rem 1rem;
  top: 12px;
  left: 12px;
}

header {
  z-index: 50;
  transition: transform .22s;
  position: fixed;
  inset: 0 0 auto;
  transform: translateY(0);
}

header.is-hidden {
  transform: translateY(-100%);
}

.nav {
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  background: color-mix(in oklab, var(--bg) 70%, black 5%);
  border-bottom: 1px solid color-mix(in oklab, var(--text) 15%, transparent);
}

.nav__inner {
  justify-content: space-between;
  align-items: center;
  padding-block: 10px;
  display: flex;
}

.nav__list {
  align-items: center;
  gap: 8px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav a[aria-current="true"] {
  background: color-mix(in oklab, var(--brand) 18%, transparent);
  border-radius: 10px;
  padding: 8px 12px;
}

.brand {
  border: 1px solid color-mix(in oklab, var(--text) 20%, transparent);
  border-radius: 10px;
  padding: 8px 12px;
  font-weight: 700;
}

.hero__visual {
  aspect-ratio: 1 / 1;
  border: 1px solid color-mix(in oklab, var(--text) 15%, transparent);
  background: radial-gradient(1200px 800px at 15% 20%, #6c5ce740, #0000), radial-gradient(900px 700px at 85% 70%, #0ea5e950, #0000);
  border-radius: 20px;
}

.card {
  border: 1px solid color-mix(in oklab, var(--text) 15%, transparent);
  background: none;
  border-radius: 16px;
  padding: 16px;
  transition: box-shadow .2s, transform .2s;
}

.card:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 24px #0000001a;
}

.tags {
  flex-wrap: wrap;
  gap: 6px;
  margin-top: 8px;
  display: flex;
}

.tags span {
  border: 1px solid color-mix(in oklab, var(--text) 20%, transparent);
  border-radius: 999px;
  padding: 2px 8px;
  font-size: 12px;
}

.timeline {
  border-left: 2px solid color-mix(in oklab, var(--text) 20%, transparent);
  margin-top: 12px;
  padding-left: 18px;
  position: relative;
}

.timeline li {
  margin-bottom: 18px;
}

.timeline li:before {
  content: "";
  width: 12px;
  height: 12px;
  background: var(--brand);
  border-radius: 9999px;
  position: absolute;
  left: -7px;
  transform: translateY(6px);
}

.contact input, .contact textarea {
  width: 100%;
  color: inherit;
  background: none;
  border: 1px solid #ccc;
  border-radius: 12px;
  margin-top: 6px;
  padding: 10px 12px;
  display: block;
}

.footer {
  opacity: .75;
  padding: 48px 0;
}

@media (prefers-color-scheme: dark) {
  :root {
    --bg: #0b0b0c;
    --text: #f3f3f3;
  }

  .nav {
    background: #0b0b0c80;
  }

  .card {
    border-color: #fff2;
  }

  .btn {
    border-color: #ffffff2a;
  }
}

